/*
 * https://www.contentful.com/developers/docs/javascript/tutorials/rendering-contentful-rich-text-with-javascript/
 * https://github.com/contentful/rich-text/tree/master/packages/rich-text-react-renderer
 * */
import React, { FunctionComponent } from 'react';
import ClassNames from './RichText.module.scss';
import classNames from 'classnames';
import { BLOCKS, INLINES } from '@contentful/rich-text-types';
import { documentToReactComponents, Options } from '@contentful/rich-text-react-renderer';
import AppLink from '../../../../util-components/AppLink/AppLink';

interface Props {
    className?: string;
    data: any;
}

const Text: FunctionComponent = ({ children }) => <p className="align-center">{children}</p>;

const Paragraph = (node, children) => {
    return <Text>{children}</Text>;
};

function constructRichText(doc) {
    const options: Options = {
        renderNode: {
            [BLOCKS.EMBEDDED_ENTRY]: function EmbeddedEntry(node) {
                return <div>{JSON.stringify(node)}</div>;
            },
            [BLOCKS.PARAGRAPH]: Paragraph,
            [INLINES.HYPERLINK]: function HyperLink(node) {
                return <AppLink to={node.data.uri}>{(node.content[0] as any).value}</AppLink>;
            },
            [INLINES.ENTRY_HYPERLINK]: function EntryHyperLink(node) {
                return <AppLink toId={node.data.target.sys.id}>{(node.content[0] as any).value}</AppLink>;
            },
        },
    };

    return documentToReactComponents(doc, options);
}
const RichText: FunctionComponent<Props> = ({ className, data }) => {
    return <div className={classNames(ClassNames.RichText, className)}>{constructRichText(data)}</div>;
};

export default RichText;
