import React, { FunctionComponent } from 'react';
import ClassNames from './Image.module.scss';
import classNames from 'classnames';

interface Props {
    className?: string;
    data: any;
}

const Image: FunctionComponent<Props> = ({ className, data }) => {
    return (
        <div className={classNames(ClassNames.Image, className)}>
            <img className={ClassNames.img} src={data.fields.file.url} />
        </div>
    );
};

export default Image;
